.header {
    position: relative;
    width: 100%;
    background: #fff;
    border-bottom: 1px solid rgba(221, 221, 221, 0.3);
    vertical-align: middle;

}

.header .inner {
    display: flex;
    justify-content: space-between;
}



.header .inner h1 a {
    display: inline-block;
    line-height: 90px;
}

.header .inner h1 img {
    width: 120px;
    height: 70px;
    object-fit: contain;

}

.header .inner .gnb {
    line-height: 90px;
}

.header .inner .gnb>ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 60px;
}

.header .inner .gnb>ul>li:hover .drop_menu {
    display: flex;
    gap: 30px;
    justify-content: center;
    height: 100%;
}

.header .inner .gnb>ul>li>a {
    display: block;
    font-size: 18px;
    font-weight: 500;
}

.header .drop_menu {
    position: absolute;
    top: 91px;
    left: 0;
    right: 0;
    height: 0;
    display: none;
    background: #fff;
    justify-content: center;

    z-index: 1000;
}

.header .inner .gnb>ul>li:first-child .drop_menu {
    margin-left: -300px;
}

.header .inner .gnb>ul>li:nth-child(2) .drop_menu {
    margin-left: -110px;
}

.header .inner .gnb>ul>li:nth-child(3) .drop_menu {
    margin-left: -35px;
}

.header .inner .gnb>ul>li:nth-child(4) .drop_menu {
    margin-right: -275px;
}

.header .inner .gnb>ul>li:nth-child(5) .drop_menu {
    margin-right: -510px;
}

.header .inner .gnb>ul>li:nth-child(6) .drop_menu {
    margin-right: -740px;
}