.footer {
    background: #f8f8f8;
}

.footer .top .inner {
    padding: 30px 0;
    border-bottom: 1px solid#ddd;
}

.footer .top .inner ul {
    display: flex;
    gap: 30px;
    justify-content: flex-start;
    font-size: 15px;
    font-weight: 300;
    color: #555;
}

.footer .top .inner ul>li {
    position: relative;
}

.footer .top .inner ul>li~li::before {
    content: "";
    position: absolute;
    top: 50%;
    left: -15px;
    transform: translate(0, -50%);
    width: 1px;
    height: 10px;
    background: #999;
}

.footer .top .inner ul>li>a {
    display: block;
}

.footer .top .inner ul .f_list_fir a {
    color: #222;

}

.footer .bottom {
    padding: 50px 0;

}

.footer .bottom .inner {
    display: flex;
    gap: 30px;
}

.footer .bottom .inner figure img {
    width: 120px;
}

.footer .bottom .inner address {
    padding: 20px 0;
}

.footer .bottom .inner address ul {
    display: flex;
    gap: 30px;
    justify-content: flex-start;
    flex-wrap: wrap;
    font-size: 15px;
    font-weight: 300;
    color: #555;
    margin: 0 0 20px 0;
}

.footer .bottom .inner address ul>li {
    position: relative;
}

.footer .bottom .inner address ul>li~li::before {
    content: "";
    position: absolute;
    top: 50%;
    left: -15px;
    transform: translate(0, -50%);
    width: 1px;
    height: 10px;
    background: #999;
}

.footer .bottom .inner address ul>li>a {
    display: block;

}

.copy {
    color: #777;
}